import { createSlice } from '@reduxjs/toolkit';
 const Allposts = createSlice({
    name: "Allpost",
    initialState: [] ,
    reducers: {
        GetAll : (state,action)=> {
             return action.payload;
        },
        LikePost: (state, action) => {
          const foundedIndex = state.findIndex((post)=>post._id === action.payload.postId )
          const foundedpost = state.slice(foundedIndex-1,foundedIndex)
          console.log(foundedIndex,foundedpost._id)
    },
    UnlikePost: (state, action) => {
       const foundedIndex = state.findIndex((post)=>post._id === action.payload.postId )
          const foundedpost = state.slice(foundedIndex-1,foundedIndex)
          console.log(foundedIndex,foundedpost._id)
    },
  UpdatePost : (state,action)=>{
    return state.map((post) => {
      if (post._id === action.payload.postId) {
        return{
          ...post,
          message:action.payload.message
        }
      }
      return post
    })
  },
  DeletePost : (state,action)=>{
    return state.filter((post)=>post._id !== action.payload)
    // console.log(action.payload)
  }
  ,
    EditComment: (state, action) => {
      return state.map((post) => {
        if (post._id === action.payload.postId) {
          return {
            ...post,
            comments:post.comments.map((comment)=>{
              if(comment._id === action.payload.commentId){
                return{
                  ...comment,
                  text:action.payload.text
                }
              }else{
                return comment
              }
            }),
          };
        }
        return post
      });
      // console.log(action.payload)
    },

    DeleteComment: (state, action) => {
      return state.map((post) => {
        if (post._id === action.payload.postId) {
          return {
            ...post,
            comments:post.comments.filter((comm) => comm._id !== action.payload.commentId)
          };
        } else return post
       
      });
      // console.log(action.payload)
    },
    }
})
export const GetAllposts = Allposts.actions
export default Allposts.reducer;