import {createContext,useState,useEffect,useCallback,useRef} from "react";
import { getRequest} from "../utils/Services";
import { followerFollowings,followerFollowingsFull } from "../Utils";
import {generateUniqueId,replace} from "../utils/Services"
import{useSelector} from"react-redux"
import axios from "axios"
import {io} from "socket.io-client"
import{SERVER_URL} from "../../Domain"
export const ChatContext = createContext();
export const ChatContextProvider = ({children})=>{
	const [userChats,setuserChats]  = useState(null)
	const [isUserChatsLoading,setisUserChatsLoading]  = useState(false)
	const [userChatsError,setuserChatsError]  = useState(null)
    const user = useSelector(state=>state.userInfo);
    const usersData = useSelector(state=>state.getusers);
    const [potentielsChats,setPotentielsChats] = useState([])
    const [currentChat,setCurrentChat] = useState(null)
    const [currentUser,setcurrentUser] = useState(user)
    const [messages,setMessages] = useState(null)
    const [isMessageLoading,setIsMessageLoading] = useState(false)
    const [messageError,setMessageError] = useState(null)
    const [sendTextmessageError,setsendTextmessageError] = useState(null)
    const [newMessage,setNewMessage] = useState(null)
    const [socket,setSocket] = useState(null)
    const [onlineUsers,setOnlineUsers] = useState([])
    const [notifications,setNotifications]=useState([])
    const [lastetMessageSend,setlastetMessageSend] = useState()
    const [textMessage,setTextMessage]=useState("")
    const [statusTyping,setStatus]=useState()
    const InputRef = useRef()
    const [typing,setTyping] = useState("isNotTyping")
    var timeoutId=undefined;
    useEffect(()=>{
        const newSocket = io(SERVER_URL)
        setSocket(newSocket)
        return () => newSocket.disconnect()
    },[user])

    useEffect(()=>{
        if(socket === null) return
        if(user?._id)socket.emit("newuser", user?._id)  
            socket.on("onLineusers",(res)=>{
           setOnlineUsers(res)
            })
        return ()=>{
            socket.off("onLineusers")
        }
    },[socket,user])

    //send message to the socket

    useEffect(()=>{
        if(socket === null) return
        const recepient = currentChat?.users?.find((c)=>c?._id !== user?._id)
         socket.emit("new message",{...newMessage,recepient})
        },[newMessage])


    // receive message from socket and notification


        useEffect(()=>{
        if(socket === null) return

       socket.on("message received",res=>{
        if(currentChat?._id !== res?.chat?._id) return
        if(res?.sender?._id === user?._id) return
            setMessages((prev)=>[...prev,res])
            setlastetMessageSend(res)
       })

   socket.on("akiraNotification",res=>{
    const isChatOpen = currentChat?.users?.some((user)=>user?._id === res?.senderId)
    if(isChatOpen){
        setNotifications((prev)=>[{...res,isRead:true},...prev])
    }else{
        setNotifications(prev => [res,...prev])
    }
   })
   return ()=>{
    socket.off("message received")
    socket.off("akiraNotification")
   }
    },[socket,currentChat])


//receive message from socket while user is typing..

        useEffect(()=>{
        if(socket === null) return
       socket.on("istyping",(data)=>{
        setStatus(data)
       })
       socket.on("isnottyping",(data)=>{
        setStatus(data)
       })
        return ()=>{
        socket.off("istyping")
        socket.off("isnottyping")
        }
        },[socket])




	useEffect(()=>{
     const getUserChats = async()=>{
     if(user?._id){
     	setisUserChatsLoading(true)
     	setuserChatsError(null)
     	const response = await getRequest(`${SERVER_URL}/api/chat/${user?._id}`)
     	setisUserChatsLoading(false)
     	if(response.error){
     		return setuserChatsError(response)
     	}
     	setuserChats(response)
     }
     }
     getUserChats()


	},[user,notifications,newMessage])


 useEffect(()=>{
    	const myfollowerFollowings = followerFollowings(user?._id,usersData);
const pChats = myfollowerFollowings.filter((id)=>{
    	let isCreatedChat = false;

	    	if(userChats){
    		isCreatedChat = userChats?.some((chat)=>{
    			if(chat.isCreatedChat) return
    			return chat?.users[0]?._d=== id || chat?.users[1]?._id===id;	
    		})
    	}
return !isCreatedChat;
})
setPotentielsChats(followerFollowingsFull(usersData,pChats))
    },[userChats,usersData,user._id])


const updatecurrentChat = useCallback((chat)=>{
setCurrentChat(chat)
InputRef?.current?.classList?.toggle("chat-selected")

},[])
//set currentuser to the real value
useEffect(()=>{
if(user){
    setcurrentUser(user)
}
},[currentChat])

const sendTexMessage = useCallback(async(textMessage,currentChat,setTextMessage)=>{
    if(!textMessage) return
const fakeIdMessage = generateUniqueId()
const recepient = currentChat?.users?.find((c)=>c?._id !== user?._id) 

        socket?.emit('notyping', {            
            typing:false,
            currentChat:currentChat._id,
            currentUser:currentUser._id,
            recepient
        })
    var draftMessage = {
        "sender":currentUser,
        "message":textMessage,
         "type":"msg",
         "incoming":false,
         "chat":currentChat,
         "createdAt":new Date(),
         "idMessage":fakeIdMessage,
         "status":"PENDING"
    }
    setlastetMessageSend(draftMessage)
    setMessages((prev)=>[...prev,draftMessage])
        setTextMessage('')
            await axios({
            method: "post",
            url: `${SERVER_URL}/api/message`,
            withCredentials: true,
            data:{
                chatId:currentChat?._id,
                message:textMessage,
                type:"msg",
                incoming:false,
                status:"DERIVED",
                idMessage:fakeIdMessage
            }
            })
            .then((res) => {
            setNewMessage(res?.data)
            setlastetMessageSend(res?.data)
            // setMessages((prev)=>[...prev,res?.data])
            setMessages((prev)=>{
                const foundIndex = prev.findIndex((item)=>item.idMessage === fakeIdMessage)
                return replace(prev,foundIndex,res.data)
            })
            })
            .catch((error) => {
            setsendTextmessageError(error)
            })

},[currentUser])
 const creatChat = useCallback(
 	async(userId) => {
        await axios({
        method: "post",
        url: `${SERVER_URL}/api/chat`,
        withCredentials: true,
        data:{userId}
      })
        .then((res) => {
        	setuserChats((prev)=>[...prev,res.data])
        })
        .catch((error) => {
       // navigate("/login")
      })
 	},[])
       const getMessages =useCallback(async()=>{
        if(currentChat){
      setIsMessageLoading(true)
      setMessageError(null)
       await axios({
        method: "get",
        url: `${SERVER_URL}/api/message/${currentChat?._id}`,
        withCredentials: true,
      })
        .then((res) => {
        setMessages(res.data)
         setIsMessageLoading(false)   
        })
        .catch((error) => {
         setMessageError(error)
         setIsMessageLoading(false)
      })            
        }
     },[currentChat?._id])
    useEffect(()=>{
     getMessages()
    },[getMessages])

    const markThisNotificationAsRead = useCallback((thisUserNotifications,notifications)=>{
       const mNotifications = notifications.map((el)=>{
        let notification;

        thisUserNotifications.forEach((n)=>{
            if(n.senderId === el.senderId){
                notification = {...n,isRead:true}
            }else{
                notification = el
            }
        })

        return notification
       })
       setNotifications(mNotifications)
    },[])
//====================================================typing logic code ========================================================================

        const typingTimeout = useCallback(()=>{
            setTyping("isNotTyping")
        },[])

       let callbackTimestamp = Date.now() + 5000;

        const sendTyping = useCallback(()=>{
        if(textMessage){
             setTyping("typing")
             timeoutId = setTimeout(typingTimeout, 5000);
            if (!timeoutId) return;
            clearTimeout(timeoutId);
            callbackTimestamp += 1000;
            var bb = callbackTimestamp  - new Date()
            timeoutId = setTimeout(typingTimeout, bb);

        }
        },[textMessage])


        useEffect(()=>{
        sendTyping()
        },[sendTyping])
           

        useEffect(()=>{
        if(socket === null) return
        const recepient = currentChat?.users?.find((c)=>c?._id !== user?._id) 
        if(typing === "typing"){
        socket?.emit('typing', {
            typing:true,
            currentChat:currentChat._id,
            currentUser:currentUser._id,
            recepient
        })
        }else{
        socket?.emit('notyping', {            
            typing:false,
            currentChat:currentChat._id,
            currentUser:currentUser._id,
            recepient
        })     
        }
        },[typing])
//==================================================== ends of typing logic code ========================================================================

	return <ChatContext.Provider
     value={
        {
        userChats,
        isUserChatsLoading,
        textMessage,
        setTextMessage,
        userChatsError,
        potentielsChats,
        creatChat,
        updatecurrentChat,
        currentChat,
        lastetMessageSend,
        messages,
        isMessageLoading,
        messageError,
        sendTexMessage,
        onlineUsers,
        notifications,
        markThisNotificationAsRead,
        InputRef,
        statusTyping
}
    }>
	{children} 
	</ChatContext.Provider>
}